body {
  margin: 0;
}

.test-runner{font-family:'Source Sans Pro', Arial, sans-serif;font-size:16px;line-height:1.4;height:100vh;width:100vw;display:flex;flex-direction:column;position:relative;overflow:hidden}.test-runner .jump-menu{position:absolute;top:3rem}.top-bar{height:3rem;display:flex;flex-direction:row;justify-content:flex-start;align-items:center;width:100%;z-index:1}.top-bar .menu-button{border:none;background:none;margin-left:15px}.top-bar .menu-button svg{font-size:24px;font-weight:bold}.top-bar h1{font-size:1rem;font-weight:400;line-height:1.2rem}.top-bar h1 .test-breadcrumb{display:inline}.top-bar h1 .test-breadcrumb span{margin:0 0.4rem}.top-bar h1 .timer{border-radius:8px;padding:2px 4px;font-size:0.8rem}.top-bar .toolMenu{margin-left:auto;margin-right:15px;cursor:pointer}.top-bar .toolMenu>svg{font-size:24px;font-weight:bold;color:#767676}.top-bar .toolMenu .popup-menu{background:white;box-shadow:0 0 4px 1px rgba(0,0,0,0.2);width:12rem;position:absolute;top:30px;right:15px;opacity:1;transition:0.4s opacity}.top-bar .toolMenu .popup-menu.qti-visually-hidden{opacity:0}.top-bar .toolMenu .popup-menu button{display:block;padding:0.4rem;width:100%;background:white;border-bottom:1px solid #999}.top-bar .toolMenu .popup-menu button:hover,.top-bar .toolMenu .popup-menu button:focus{background-color:#cce0f8}.top-bar .toolMenu .popup-menu button:focus{outline:1px solid #767676;outline-offset:-4px}@-webkit-keyframes translateRight{0%{transform:translateX(0)}49.9%{transform:translateX(2000px)}50%{transform:translateX(-2000px)}100%{transform:translateX(0)}}@keyframes translateRight{0%{transform:translateX(0)}49.9%{transform:translateX(2000px)}50%{transform:translateX(-2000px)}100%{transform:translateX(0)}}@-webkit-keyframes translateLeft{0%{transform:translateX(0)}49.9%{transform:translateX(-2000px)}50%{transform:translateX(2000px)}100%{transform:translateX(0)}}@keyframes translateLeft{0%{transform:translateX(0)}49.9%{transform:translateX(-2000px)}50%{transform:translateX(2000px)}100%{transform:translateX(0)}}main{flex-grow:1;width:100%;overflow-x:hidden;overflow-y:auto}main .qti-itemBody{width:60%;margin:0 auto;line-height:1.6}main .qti-itemBody.anim-translateRight{-webkit-animation:translateRight .6s 1;animation:translateRight .6s 1}main .qti-itemBody.anim-translateLeft{-webkit-animation:translateLeft .6s 1;animation:translateLeft .6s 1}main .qti-itemBody .qti-choice{display:flex;justify-content:space-between;margin-bottom:0.4rem}main .qti-itemBody .qti-choice.eliminated label{color:#999;text-decoration:line-through;pointer-events:none}main .qti-itemBody .qti-choice label input{margin-right:1rem}main .qti-itemBody .qti-choice button{width:11rem}.bookmark-tool{position:absolute;right:1rem;bottom:5rem}.bookmark-tool button{line-height:50px;font-size:1.2rem}.button-large-circle{height:48px;min-width:48px;border-radius:28px;margin:12px;line-height:52px;cursor:pointer}.button-large-circle.scaling:hover svg{transform:scale(1.2)}.testNav{height:4rem;min-height:4rem;width:100%;overflow-y:hidden;display:flex;flex-direction:row;justify-content:space-around;align-items:center}.testNav button.prev{order:1}.testNav button.next{order:4}.testNav .overview-button{height:36px;width:unset;border-radius:18px;text-transform:uppercase;order:3;font-size:0.85rem;display:flex;align-items:center;line-height:1;padding:0.2rem 0.6rem}.testNav>button>svg{font-size:20px;stroke-width:3px}.testNav .testNav-sections{margin:0;display:flex;flex-wrap:nowrap;justify-content:center;order:2}.testNav .testNav-sections>li{display:inline-block}.testNav .testNav-sections fieldset{border:0;padding:0;margin:0 0.6rem;display:flex;flex-wrap:nowrap;height:100%;align-items:center}.testNav .testNav-sections fieldset ol{display:flex;flex-wrap:nowrap;height:100%;align-items:center}.testNav .testNav-sections fieldset ol .navBubble{display:inline-flex;justify-content:center;align-items:center;height:36px;width:36px;border-radius:20px;box-shadow:0 0 0 2px transparent;transition:0.25s background-